import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './components/Home';
import UserProfile from './components/UserProfile';
import LogIn from './Login';
import Debit from './components/Debit';
import Credit from './components/Credit';
import axios from 'axios';

function App() {
    const [user, setUser] = useState(
        {
            accountBalance: 14568.27,
            currentUser: {
                userName: 'joe_shmo',
                memberSince: '07/23/96',
            },
            debits: [],
            credits: [],
        }
    )

    useEffect(() => {
        async function fetchData() {
            const newUser = { ...user };
    
            axios.get("https://moj-api.herokuapp.com/debits")
                .then(response => {
                    newUser.debits = response.data;
                })
                .catch(err => console.log(err));
    
            axios.get("https://moj-api.herokuapp.com/credits")
                .then(response => {
                    newUser.credits = response.data;
                })
                .catch(err => console.log(err));
    
            setUser(newUser);
        }
    })

    function mockLogIn(logInInfo) {
        const newUser = user.currentUser;
        newUser.userName = logInInfo.userName
        setUser({ currentUser: newUser })
    }

    function addDebit(deb) {
        setUser(...user.debits, deb);
    }

    function addCredit(cred) {
        const newUser = { ...user };
        newUser.credits = newUser.credits.concat(cred);
        setUser(newUser);
        updateBal();
    }

    function updateBal() {
        let balance = 0;
        user.credits.map(cred => (
            balance -= cred.amount
        ))
        user.debits.map(deb => (
            balance += deb.amount
        ))
        const newUser = { ...user };
        newUser.accountBalance = balance;
        setUser(newUser)
    }

    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/bank-of-react/" component={Home}>
                    <Home user={user} />
                </Route>
                <Route exact path="/bank-of-react/login">
                    <LogIn mockLogIn={mockLogIn} />
                </Route>
                <Route exact path="/bank-of-react/userProfile">
                    <UserProfile user={user.currentUser} />
                </Route>
                <Route exact path="/bank-of-react/Debit">
                    <Debit debits={user.debits} />
                </Route>
                <Route exact path="/bank-of-react/Credit">
                    <Credit addCredit={addCredit} credits={user.credits} />
                </Route>
            </Switch>
        </BrowserRouter>
    );

}

export default App;